.road-show-block {
    background: -webkit-gradient(linear, left top, left bottom, from(#2c2547), color-stop(#464087), to(#62add3));
    background: -webkit-linear-gradient(#2c2547, #464087, #62add3);
    background: -o-linear-gradient(#2c2547, #464087, #62add3);
    background: linear-gradient(#2c2547, #464087, #62add3);
    width: 100%;
    height: 100%;
    padding-bottom: 70px;
}

.wrapper-road {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    width: 103px;
    text-align: center;
}

.road__line {
    top: 14px;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fff;
}

.road__circles {
    margin-top: 500px;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.road__circle {
    cursor: pointer;
    border-radius: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 30px;
    height: 30px;
    background-color: #fff;
    margin-bottom: 15px;
}

.road-show-block .slick-next, .road-show-block .slick-prev {
    display: none!important;
}

.road__settings {
    width: 100%;
    padding: 0 80px;
}

.road-show-a {
    color: inherit;
    width: 100%;
}

.road-show-popup {
    position: absolute;
    width: 345px;
    bottom: 150px;
    left: calc(213px + 432 * ((100vw - 768px) / 1152));
}

.road-popup-text {
    font-size: 14px;
    padding: 20px;
    background-color: #fff;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    min-height: 290px;
    width: 100%;
    position: relative;
}

.road-show__report {
    position: absolute;
    bottom: 20px;
}

.road-show-block .slick-initialized .slick-slide {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.road__class {
    font-size: 10px;
    text-transform: uppercase;
    color: #fff;
}

.road__date {
    font-size: 20px;
    text-transform: uppercase;
    color: #fff;
    font-weight: 500;
}

.road-popup-video {
  width: 100%;
  height: auto;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.road-show-popup .img {
  width: 100%;
  height: 160px;
  background-size: cover;
  background-position: center center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.road-popup-triangle {
    width: 0;
    height: 0;
    border-bottom: 40px solid #fff;
    border-right: 40px solid transparent;
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
    position: relative;
    bottom: 21px;
    left: 75px;
}

.road__setting {
    display: block;
}

.road__setting2,
.road__setting3,
.road__setting4,
.road__setting5,
.road__setting6 {
    display: none;
}

.statusEvent {
  color: white;
  display: -ms-inline-flexbox;
  display: inline-flex;
  padding: 5px 20px;
  border-radius: 4px;
}
.statusEvent.UPCOMING {
  background: #9b9bca;
}
.statusEvent.ENDED {
  background: #6ad46a;
}

@media screen and (max-width: 1023px) {
    .road__circles {
        margin-top: 580px;
    }
}

@media screen and (min-width: 1024px) {
    .road__circles {
        margin-top: 570px;
    }
}

@media screen and (min-width: 1001px) and (max-width: 1201px) {
    .road__setting {
        display: none;
    }

    .road__setting2 {
        display: block;
    }
}

@media screen and (min-width: 768px) and (max-width: 1000px) {
    .road__setting {
        display: none;
    }

    .road__setting3 {
        display: block;
    }
}

