@media screen and (max-width: 767px) {
    .road__circles {
        -ms-flex-pack: center;
            justify-content: center;
    }

    .road__setting {
        display: none;
    }

    .road__setting4 {
        display: block;
        padding: 0 40px;
    }

    .road-show-popup {
        left: auto;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-align: center;
            align-items: center;
    }

    .road-popup-triangle {
        left: 0;
    }
}

@media screen and (max-width: 450px) {
    .road__setting {
        display: none;
    }

    .road__circles {
        -ms-flex-pack: center;
            justify-content: center;
    }

    .road__setting4 {
        display: block;
        padding: 0 20px;
    }

    .road-show-popup {
        width: 90%;
    }
